/* Menu Page Classes*/

.box-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center; /* Center the container horizontally */
    margin: 10px auto; /* Margin on top and bottom, and auto margins on left and right */
}
  
.box {
    width: 200px;
    height: 200px;
    background-color: #b8d8be;
    margin: 10px;
    border-radius: 15%;
}